.block30{
  padding: 90px 38px 38px;
  background: var(--color-white-80);
  .h1{
    text-align: center;
  }
  .navs{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 0 0;
  }
  .swiper{
    padding: 32px 0 0;
    .swiper-slide{
      position: relative;

      height: 44.19vw;
      overflow: hidden;

      background-color: transparent;
      background-image: url('../../img/contract_production/s3/bg.png');
      background-position: center bottom;
      background-repeat: no-repeat;
      background-size: cover;

      .tooltip{
        position: absolute;
        top: 20px;
        left: 20px;

        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-start;
        align-items: flex-start;

        z-index: 10;
      }
      .tooltip__btn{
        flex: 0 0 auto;

        width: 50px;
        height: 50px;
        border-radius: 25px;

        border: none;
        outline: none;

        background-color: var(--color-text-hover);
        background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6.99982 0.181885L6.99982 13.8182M0.181641 7.00007L13.818 7.00007' stroke='white' stroke-width='2'/%3E%3C/svg%3E");
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 14px 14px;
      }
      .tooltip-box{
        flex: 0 0 auto;
        width: 292px;
        margin-left: 12px;
        padding: 20px 16px;

        border-radius: 15px;
        background: var(--color-white-70);
        backdrop-filter: blur(15px);

        transform: scaleY(0);
        transform-origin: top;
        transition: transform 0.2s ease;
      }
      .tooltip.active{
        .tooltip-box{
          transform: scaleY(1);
        }
      }
      .tooltip-cont + .tooltip-cont{
        margin-top: 22px
      }
      .tooltip__title{
        color: var(--color-text-hover);
        font-family: 'Manrope';
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 22px;
      }
      .tooltip-item{
        color: var(--color-text-main);
        font-family: 'Manrope';
        font-size: 12px;
        font-style: normal;
        font-weight: 300;
        line-height: 22px;
      }
      .bold{
        color: var(--color-text-main);
        font-family: 'Manrope';
        font-size: 12px;
        font-style: normal;
        font-weight: 700;
        line-height: 22px;
      }
      .swiper-slide-gallery{
        position: absolute;
        height: 100%;
        width: 100%;
        bottom: 0;
        left: 0;

        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: flex-end;

        padding-bottom: 3.5vw;
        overflow: hidden;

        .swiper-slide-gallery-wrap{
          display: flex;
          flex-flow: row nowrap;
          justify-content: center;
          align-items: flex-end;

          height: 100%;

          overflow-x: auto;
          overflow-y: hidden;

          img{
            display: block;
            margin-left: 1vw;
            margin-right: 1vw;
          }
        }
      }
    }
  }
}


