.block19{
  position: relative;
  z-index: 1;
  .swipernavs{
    position: absolute;
    top: 14px;
    right: 0;
  }
  .swiper{
    padding: 20px 0 0;
    .swiper-wrapper{
      align-items: stretch;
      .swiper-slide{
        height: auto;
        padding: 30px 20px;
        background: var(--color-white-80);
        img{
          width: 67px;
          height: 67px;
        }
        .h3,
        .desc{
          padding: 20px 0 0;
        }
      }
    }
  }
}