.block12{
  position: relative;
  overflow: hidden;
  video,
  img{
    display: block;
    width: 100%;
  }
  .info{
    position: absolute;
    bottom: 24px;
    left: 24px;
    max-width: 430px;
    padding: 30px;
    background: var(--color-white-80);
    backdrop-filter: blur(10px);
    &.dark{
      background: linear-gradient(45deg, var(--color-black-20) 0%, var(--color-black-50) 100%);
      .h3,
      .desc{
        color: var(--color-white);
      }
    }
    .desc{
      padding: 8px 0 0;
    }
  }
  .article{
    padding: 40px 0 0;
    overflow: hidden;
    .desc{
      position: absolute;
      bottom: 40px;
      left: 40px;
      max-width: 844px;
      font-size: 18px;
      line-height: 28px;
      color: var(--color-white);
    }
  }
}