.block34{
  ul{
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    z-index: 1;
    li{
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      gap: 20px;
      padding: 30px;
      background: var(--color-white-80);
      &:hover{
        transform: scale(.97);
        .info{
          .h3{
            color: var(--color-text-hover);
          }
        }
      }
      .info{
        flex: 1 1 auto;
        .desc{
          padding: 10px 0 0;
          line-height: 24px;
        }
        a.btn{
          margin: 10px 0 0;
        }
      }
      .img{
        max-width: 42%;
        min-width: 42%;
      }
    }
  }
}