.block6{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  .item{
    position: relative;
    &:first-child{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      .h1,
      .text,
      .btn,
      .flask{
        position: relative;
        z-index: 1;
      }
      .text{
        max-width: 600px;
        padding: 32px 0 0;
        font-weight: 300;
      }
      .btn{
        margin: 48px 0 0;
      }
      .flask{
        max-width: 110px;
        margin: auto auto 0;
        transform: rotate(28deg);
        img{
          max-width: 100%;
        }
        .filter{
          position: absolute;
          bottom: 2px;
          left: 20px;
          width: 75px;
          height: 81%;
          border-radius: 0 0 50px 50px;
          mix-blend-mode: lighten;
          overflow: hidden;
          transition: none;
          .fill{
            position: absolute;
            bottom: -50px;
            left: -200%;
            width: 600%;
            height: 0;
            background: var(--color-text-hover);
            transform: rotate(-24deg);
            transition: none;
            svg{
              position: relative;
              width: 61%;
              height: 9%;
              min-height: 3px;
              max-height: 150px;
              margin: -10px 0 0;
              transition: none;
              use{
                animation: waves 5s cubic-bezier(.55,.5,.45,.5) infinite;
                &:nth-child(1){
                  animation-delay: -2s;
                  animation-duration: 7s;
                }
                &:nth-child(2){
                  animation-delay: -3s;
                  animation-duration: 10s;
                }
                &:nth-child(3){
                  animation-delay: -4s;
                  animation-duration: 13s;
                }
                &:nth-child(4){
                  animation-delay: -5s;
                  animation-duration: 20s;
                }
              }
            }
          }
        }
      }
      .bganimation{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
    &:last-child{
      .h2{
        line-height: 44px;
      }
      ul{
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 74px;
        padding: 64px 0 0;
        li{
          display: flex;
          align-items: center;
          .info{
            flex: 1 1 auto;
            .desc{
              padding: 8px 0 0;
            }
          }
        }
      }
    }
  }
}

@keyframes waves{
  0% {
    transform: translate3d(-90px,0,0);
  }
  100% {
    transform: translate3d(85px,0,0);
  }
}