.block4{
  position: relative;
  z-index: 1;
  .swiper{
    .swiper-wrapper{
      align-items: stretch;
      .swiper-slide{
        height: auto;
        padding: 38px 20px;
        border: 1px solid var(--color-black-10);
        background: var(--color-white-60);
        transition-property: all;
        &:hover{
          border-color: var(--color-white);
          background: var(--color-white);

        }
        img{
          width: 67px;
          height: 67px;
        }
        h3{
          padding: 40px 0 0;
          font-weight: 500;
        }
        .desc{
          padding: 12px 0 0;
        }
      }
    }
    .actions{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 30px 0 0;
    }
  }
}