.block22{
  ul{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
    padding: 30px 0 0;
    li{
      a{
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 30px 24px;
        background: var(--color-white);
        &:hover{
          transform: scale(.97);
        }
        .date{
          font-weight: 300;
        }
        h3{
          padding: 22px 0 70px;
        }
        img{
          margin: auto 0 0;
        }
      }
    }
  }
  .actions{
    display: flex;
    justify-content: center;
    padding: 40px 0 0;
  }
}