.block29{
  ul{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 45px 66px;
    padding: 66px 0 0;
    li{
      display: flex;
      align-items: center;
      gap: 28px;
      .text{
        flex: 1 1 auto;
        font-weight: 300;
      }
    }
  }
  .ext{
    padding: 56px 0 0;
  }
}