.block1{
  display: grid;
  grid-template-columns: auto calc((100vh - 146px - 15px) / 2);
  gap: 14px;
  min-height: calc(100vh - 131px);
  &.fullscreen{
    z-index: 2000;
  }
  .item{
    position: relative;
    &:nth-child(1){
      z-index: 2;
      .swiper{
        position: absolute;
        &.main{
          top: 0;
          left: 0;
          transition: all .5s ease;
          &.animate1{
            top: -115px;
            left: -48px;
            width: calc(100% + 48px);
            height: 100vh;
            border-radius: 0;
            z-index: 2000;
          }
          &.animate2{
            width: 100Vw;
            height: 100vh;
          }
          .swiper-slide{
            &-active{
              .box{
                transform: scale(1)!important;
              }
            }
            .box{
              position: absolute;
              top: 0;
              left: 0;
            }
          }
        }
      }
    }
    &:nth-child(2){
      display: grid;
      grid-template-rows: 1fr 1fr;
      gap: 16px;
      z-index: 1;
      a{
        position: relative;
        display: flex;
        flex-direction: column;
        min-width: 0;
        padding: 35px 32px 26px;
        border-radius: 15px;
        background: var(--color-white-70);
        transform: scale(1);
        transition: transform .7s;
        &:hover{
          transform: scale(.97);
        }
        .gallery{
          position: relative;
          width: 66%;
          margin: auto auto 17px;
          border-radius: 50%;
          background: linear-gradient(180deg, transparent 65.85%, var(--color-black-07) 74.67%, transparent 95.12%), var(--color-gradient-gray);
          aspect-ratio: 1 / 1;
          .swiper{
            position: absolute;
            left: 50%;
            width: 100%;
            max-width: 324px;
            transform: translateX(-50%);
            .swiper-slide{
              img{
                width: 100%;
              }
            }
          }
        }
        .info{
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
          margin-top: auto;
          .h3{
            line-height: 28px;
          }
          img{
            max-width: 38px;
            min-width: 38px;
            height: 38px;
          }
        }
      }
    }
  }
}