.block18{
  ul{
    position: relative;
    display: grid;
    grid-template-columns: 58% 42%;
    gap: 16px;
    grid-template-areas: "one two" "three three";
    padding: 30px 0 0;
    z-index: 1;
    li{
      position: relative;
      overflow: hidden;
      &:hover{
        &:after{
          opacity: 1;
          visibility: visible;
        }
      }
      &:after{
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        background: var(--color-black-70);
        opacity: 0;
        visibility: hidden;
        content: "";
      }
      &:nth-child(1){
        grid-area: one;
      }
      &:nth-child(2){
        grid-area: two;
      }
      &:nth-child(3){
        grid-area: three;
      }
      img{
        width: 100%;
        height: 100%;
      }
      .info{
        position: absolute;
        bottom: 40px;
        padding: 0 30px;
        color: var(--color-white);
        z-index: 1;
        .text{
          height: 0;
          overflow: hidden;
        }
      }
    }
  }
}