.block16{
  .info{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: calc(100Vh - 165px);
    padding: 0 0 165px;
    text-align: center;
    h1{
      position: relative;
      z-index: 1;
    }
    .text{
      position: relative;
      margin: 0 auto;
      padding: 10px 0 0;
      font-weight: 300;
      z-index: 1;
    }
    img{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      transition: none;
      z-index: 0;
    }
  }
  .h3{
    position: relative;
    z-index: 1;
    p{
      padding: 0 0 10px;
      &:last-child{
        padding: 0;
      }
      span{
        color: var(--color-text-hover);
      }
    }
  }
}