.block2{
  position: relative;
  padding: 70px 40px 50px;
  background: var(--color-white-80);
  z-index: 1;
  ul{
    display: grid;
    gap: 32px;
    grid-template-columns: auto auto auto auto;
    padding: 44px 0 0;
    li{
      .label{
        margin: 0 0 48px;
        padding: 0 0 10px;
        border-bottom: 1px solid var(--color-black-10);
        font-weight: 300;
        color: var(--color-text-gray);
      }
      .number{
        font-size: 64px;
        font-weight: 500;
        line-height: 72px;
        letter-spacing: -2px;
      }
      .desc{
        padding: 16px 0 0;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.16px;
      }
    }
  }
}