.block8{
  .swiper{
    padding: 40px 0 0;
    .swiper-wrapper{
      align-items: stretch;
      .swiper-slide{
        display: grid;
        grid-template-rows: 1fr;
        height: auto;
        ul{
          display: grid;
          grid-template-columns: 1fr;
          li{
            display: grid;
            grid-template-columns: 1fr;
            min-height: 50%;
            padding: 0 0 16px;
            &:last-child{
              padding: 0;
            }
            a{
              flex: 1 1 auto;
              flex-flow: row nowrap;
              gap: 16px;
              width: 100%;
              span{
                &.img{
                  max-width: 190px;
                  min-width: 190px;
                  height: 190px;
                  padding: 0;
                }
              }
            }
          }
        }
        a{
          display: flex;
          flex-flow: column-reverse nowrap;
          justify-content: flex-end;
          padding: 30px 24px;
          background: var(--color-white);
          &:hover{
            transform: scale(.97);
          }
          span{
            display: block;
            &.img{
              width: 100%;
              padding: 10px 0 0;
              overflow: hidden;
            }
            &.info{
              flex: 1 1 auto;
              .date{
                font-weight: 300;
              }
              h3{
                padding: 32px 0 0;
              }
              .desc{
                padding: 10px 0 0;
              }
            }
          }
        }
      }
    }
    .actions{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 30px 0 0;
    }
  }
}