.block36{
  .swiperscroll{
    .swiper-slide{
      width: auto;
    }
    .swiper-scrollbar{
      display: none;
    }
  }
  .swiperparams{
    overflow: visible;
    .swiper-wrapper{
      height: auto!important;
    }
    .swiper-slide{
      .databox{
        padding: 80px 0 0;
        &.description{
          .desc{
            padding: 20px 0 0;
            *{
              font-size: 18px;
              font-weight: 300;
              line-height: 28px;
            }
          }
        }
        &.params{
          ul{
            padding: 30px 0 0;
            li{
              display: flex;
              align-items: center;
              gap: 30px;
              padding: 0 0 40px;
              &:last-child{
                padding: 0;
              }
              .desc{
                *{
                  font-size: 18px;
                  font-weight: 300;
                  line-height: 28px;
                }
              }
            }
          }
        }
        &.components{
          ul{
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 16px;
            padding: 30px 0 0;
            li{
              padding: 30px 20px;
              background: var(--color-white-80);
              &:hover{
                transform: scale(.97); 
              }
              .h2{
                color: var(--color-text-hover);
              }
              .desc{
                line-height: 24px;
                &:last-child{
                  padding: 20px 0 0;
                }
              }
            }
          }
        }
        &.text{
          .desc{
            padding: 32px 0 0;
            font-size: 18px;
            line-height: 28px;
          }
        }
        &.about{
          .box{
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            gap: 16px;
            .info{
              flex: 1 1 auto;
              .h2{

              }
              .desc{
                padding: 48px 0 0;
                font-size: 18px;
                font-weight: 300;
                line-height: 28px;
              }
            }
            img{
              max-width: 50%;
              min-width: 50%;
              margin: 0;
            }
          }
          img{
            margin: 60px 0 0;
          }
        }
        &.instructions{
          position: relative;
          a.download{
            position: absolute;
            top: 80px;
            right: 0;
            gap: 16px;
            svg{
              max-width: 24px;
              min-width: 24px;
              use{
                stroke: var(--color-white);
                stroke-width: 2;
              }
            }
          }
        }
        &.method{
          .desc{
            padding: 32px 0 0;
            *{
              font-size: 18px;
              font-weight: 300;
              line-height: 28px;
            }
            img{
              margin: 30px 0 0;
            }
          }
        }
      }
    }
  }
}