.block27{
  ul{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    padding: 30px 0 0;
    li{
      a{
        display: block;
        height: 100%;
        padding: 30px 20px;
        background: var(--color-white-80);
        &:hover{
          transform: scale(.97);
        }
        span{
          display: block;
          &.h3,
          &.text{
            padding: 20px 0 0;
          }
          &.text{
            font-weight: 300;
          }
        }
      }
    }
  }
}