.block42{
  .search-result-list{
    margin-top: 50px;

    li{
      margin-bottom: 20px;
      &:last-child{
        margin-bottom: 0;
      }
      a{
        color: #121720;
        font-feature-settings: 'clig' off, 'liga' off;
        font-family: 'Manrope';
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: 32px;

        &:hover{
          color: var(--color-text-hover);
        }
      }
    }
  }
}